import _ from 'lodash'
import api from 'api'

const initState = {
  list: [],
}

export default {
  state: initState,
  // https://rematch.github.io/rematch/#/plugins/select.md
  // https://github.com/reduxjs/reselect
  selectors: (slice, createSelector, hasProps) => ({
    getTokenBySymbol () {
      // createSelector的三个参数，都是函数。
      // 最后一个是求值，前面 n-1 个代表如何提取最后一个的入参。
      // 前 n-1 个的入参，都是调用getTokenBySymbol处传的入参。
      // slice 是一个函数
      // return createSelector(
      //   slice, //slice 是一个函数,代表直接把自己对应的state 返回。
      //   (rootState, symbol) => symbol,
      //   (state, symbol) => {
      //     let { list } = state
      //     return _.find(list, token => token.symbol.toLowerCase() === symbol.toLowerCase()) || {}
      //   }
      // )

      // createSelector 是为了组合多个 selector 的写法，前 n-1 个都是其他 selector。
      // 上面的写法有点多此一举，相当于用两个 selector 分别返回两个入参。再在自己的 selector 逻辑中组合前n-1 个(实际上还是入参，未做任何transform)。
      return (state, symbol) => {
        let { list } = state.tokens
        return _.find(list, token => token.symbol.toLowerCase() === symbol.toLowerCase()) || {}
      }
    }
  }),
  reducers: {
    update: (state, payload) => ({ ...state, ...payload }),
  },
  effects: dispatch => ({
    // async fetchTokens (payload, {  }) {
    //   let tokens = await api.getContracts()
    //   this.update({ tokens })
    // }
  })
}