import _ from 'lodash';
import React, { forwardRef, useEffect, useState, useImperativeHandle, Fragment } from 'react';
import { connect } from 'react-redux';
import { useIntl, FormattedMessage } from 'react-intl';
import { SignInModal, UpsertPasswordModal } from '../Dialog'
import PromisefyConfirm from 'components/PromisefyConfirm'
import {
  Box,
  Flex,
  Text
} from 'rebass'
import utils from 'utils';
import { ENUM_ACCOUNT_STATUS } from 'utils/enum'
import { store } from 'store'
var detect = require('detect-browser').detect;

const Widget = forwardRef(({
  disabled,
  onSuccess = () => {},
  children,
  metaMask,
  dexAccount,
  signIn,
  initAccountFromMetaMask
}, ref) => {
  const intl = useIntl()

  const checkBrowser = () => {
    const browser = detect()
    // TODO 360
    let valid = _.includes(['firefox', 'chrome', '360'], browser.name)
    if (valid) return true

    return PromisefyConfirm({
      bodyStyle:{backgroundColor: '#363636', color: '#d0d0d0'},
      renderContent: () => (
        <Box px={20}>
          <Text>{intl.formatMessage({id: "modal.billPreCheck.browser.line1"})}</Text>
          <Text>{intl.formatMessage({id: "modal.billPreCheck.browser.line2"})}</Text>
          <Text>{intl.formatMessage({id: "modal.billPreCheck.browser.line3"})}</Text>
        </Box>
      ),
      maskClosable: true,
      footer: null,
      width: 800
    })
  }

  const checkMetaMaskInstalled = () => {
    let valid = metaMask.installed
    if (valid) return true

    return PromisefyConfirm({
      // title: 'MetaMask',
      renderContent: () => (
        <Box px={20}>
          <Text>{intl.formatMessage({id: "modal.billPreCheck.metamask.line1"})}</Text>
          <Text>{intl.formatMessage({id: "modal.billPreCheck.metamask.line2"})}</Text>
          <Text>{intl.formatMessage({id: "modal.billPreCheck.metamask.line3"})}</Text>
        </Box>
      ),
      maskClosable: true,
      footer: null,
      width: 800
    })
  }

  const checkNetwork = () => {
    let { isDesiredNetwork } = metaMask

    let invalidText = ''

    if (!isDesiredNetwork) {
      // invalidText = '您的MetaMask当前登录的是以太坊测试网，请切换到以太坊主网络。'
      invalidText = intl.formatMessage({id: "modal.billPreCheck.network"})
    }

    if (!invalidText) return true

    return PromisefyConfirm({
      // title: 'MetaMask',
      renderContent: () => (
        <Box px={20}>
          <Text>{invalidText}</Text>
        </Box>
      ),
      maskClosable: true,
      footer: null,
      width: 800
    })
  }

  const checkAddress = () => {
    let rootState = store.getState()
    let { address } = rootState.dexAccount

    let invalidText = ''

    if (!address) {
      invalidText = intl.formatMessage({id: "modal.billPreCheck.address"})
    }

    if (!invalidText) return true

    return PromisefyConfirm({
      // title: 'MetaMask',
      renderContent: () => (
        <Box px={20}>
          <Text>{invalidText}</Text>
        </Box>
      ),
      maskClosable: true,
      footer: null,
      width: 800
    })
  }

  const loadMetamask = async () => {
    let { enable } = metaMask
    // enable表示已经获取到了地址。如果此处为空，说明用户拒绝了授权。
    // 注意此处不是用 dexAccount的address，而是用的metaMask 的 enable。因为前者可能是缓存的，后者才能代表是否metamask 是否已获取到了地址。。
    if (!enable) {
      await initAccountFromMetaMask()
    }
  }

  const checkSignedIn = async () => {
    // loadMetamask会获取地址，并获取账户的状态。而此次可能状态已经更新。
    // 比如 account 在中继存在，需要登录，但此处dexAccount还没更新，所以要从dexAccount拿
    let rootState = store.getState()
    let { state } = rootState.dexAccount
    let signined = utils.checkLogined()
    if (signined) return true

    if (state === ENUM_ACCOUNT_STATUS.UNCREATED.getValue()) {
      try {
        await UpsertPasswordModal({type: 'create'})
        return true
      } catch (e) {
        return false
      }
    } else if (state === ENUM_ACCOUNT_STATUS.UNSIGNED.getValue()) {
      try {
        await SignInModal()
        return true
      } catch (e) {
        return false
      }
    }
  }

  useImperativeHandle(ref, () => ({
    checkBrowser,
    checkSignedIn,
    check
  }))

  const check = async () => {
    // 是否装了 metamask
    await checkBrowser()
    // 是否登录 metamask
    await checkMetaMaskInstalled()
    // 如果 metamask 还未获取到地址，则通过 MetaMask 获取地址，并初始化。（获取 MetaMask 地址，需要用户授权）
    await loadMetamask()
    // 检查网络
    await checkNetwork()
    // metamask 中是否已获取到正确的地址
    await checkAddress()
    // 地址是否已授权，且跟中继保存的公钥匹配
    await checkSignedIn()
    return true
  }

  return (
    <div onClick={async () => {
      if (disabled) return

      let isValid = await check()
      if (isValid) {
        onSuccess()
      }
    }}>
      {children}
    </div>
  )
})


const mapStateToProps = ({ metaMask, dexAccount }) => {
  return {
    metaMask,
    dexAccount
  };
};

const mapDispatchToProps = ({ dexAccount, metaMask }) => {
  return {
    initAccountFromMetaMask: metaMask.initAccountFromMetaMask,
    signIn: dexAccount.signIn
  };
};

export default connect(mapStateToProps, mapDispatchToProps, null, {forwardRef: true})(Widget)