import _ from 'lodash'
import moment from 'moment'
import { matchPath } from "react-router-dom";
import utils from 'utils'
import { ENUM_LANGUAGE_TYPE } from 'utils/enum'

export default {
  state: {
    channelName: 'wedex',
    locale: 'cn',
    market: 'LRC-ETH',

    lastTransferTime: moment().format('YYYY-MM-DD HH:mm:ss')
  },
  reducers: {
    update: (state, payload) => ({ ...state, ...payload }),
    resetLastTransferTime: (state, payload) => {
      state.lastTransferTime = moment().format('YYYY-MM-DD HH:mm:ss')
      return state
    },
  },
  effects: dispatch => ({
    async checkChannel (payload, { router }) {
      const query = utils.getUrlParams(router.location.search)
      const { channel } = query
      if (channel) {
        this.update({ channelName: channel })
      }
    },
    async checkLocale (payload, { router }) {
      let { pathname } = window.location
      let locales = _.keys(ENUM_LANGUAGE_TYPE)
      let locale = _.find(locales, locale => _.startsWith(pathname, `/${locale}/`))
      console.info({pathname, locale})

      // 如果 url 中带，则更新本地。
      if (locale) {
        this.update({ locale })
      }
      // 如果不带，默认的行为：第一次访问网站，则使用初始化的 cn；如果不是第一次，则使用缓存中的。
    },
    async checkMarket (payload, { router }) {
      // 解析路由，获取默认交易对。
      const match = matchPath(router.location.pathname, {
        path: "/:locale/trade/:market",
        exact: true,
        strict: true
      })

      // 如果 url 中带，则更新本地。
      // 重要：不能无脑使用本地缓存，否则人家精确使用 url，都没法直达对应的交易对。
      if (match && match.params.market) {
        this.update({ market: match.params.market })
      }
      // 如果不带，默认的行为：第一次访问网站，则使用初始化的 LRC-ETH；如果不是第一次，则使用缓存中的。
    },
  })
}