  // 场景二: url 正确，但是把 home 中的所有组件都注释掉，基本缓慢。说明还是 dom 问题。
  // 场景三: 打开 tvchart，但是仍然关闭 trade。基本不增长。会在大约 10 分钟后突然增加10M 左右，应该是 tvchart 自身的问题。
  // 场景四: 打开 tvchart，打开 trade，但是不显示 table。相比“场景三”有增长（15 分钟 30M。可能是candlestick 订阅的socket自己带来的更新），但是仍然比较缓慢。说明还是 trade 的 table 本身的问题。
  // 场景五: 关闭 tvchart，打开 trade，显示 table。明显每秒 dom内存（内存占用空间） 增加 3M，js（js 使用内存实际大小） 每秒增加 0.8M。持续增长了 100M后，开始下降，应该是内存回收开始生效，之后会进入一个动态平衡。
  //        原因分析：虽然只取了头部的 30 个，但是列表中实际有 130 个 dom 了。因为每次渲染时，若数组中有 rowKey 冲突，就会挂死一行数据的 dom 在列表中，估计是 ant 根据 rowKey 做了什么优化。
  //        其他细节：socket 重连一次时，会再次 1s 增加 3M。但是等 30 个 dom 绘制完成后，等一会会回收之前的 30 个 dom。应该是重新加载导致 loading，dom 全部销毁重建。
  //        这里要继续优化就得考虑共用dom。但是没必要。
  // 场景六: rowKey使用数组的 index。竟然效果奇好。对比 dom 发现，完全复用了。（当不使用 index 时，在 elements 中选中一个 dom，可以看到这个 dom 不停的往下走，上面不停的会插入新的 dom，直至到达数组末尾，然后被销毁。）
  //        这种方式下，内存明显表现非常好。而且数据的变更等表现到界面上。
  //        也有不好的地方。当头部插入 1 条数据时。整个数组的 rowKey 全部变化，虚拟 dom 树的对比会耗费 “CPU” 资源。如果不使用 ant 的 table，可能还可以进一步优化。
  //        https://segmentfault.com/a/1190000015648248
  //        此处我们主要解决内存问题，那么还是如此优化吧。
  // 场景六: table 的 rowKey 不传。
  // 场景七: trade 没有截断为最多 30 条。明显持续增加，直至崩溃。

  // cpu 占用。
  // 不停的推送期间，cpu 一直高达 10%，但是观察币安，也是如此，因此没关系。

  // utils.setImmediateInterval(async () => {
  //   let now = new Date().getTime()
