import _ from 'lodash'
import moment from 'moment'
import numeral from 'numeral'
import api from 'api';
import { ENUM_ORDER_STATUS } from 'utils/enum'
import appConfig from 'config/appConfig'
import utils, { notification } from 'utils'
import { ENUM_ACCOUNT_STATUS } from 'utils/enum'

let { pollingIntervalRatio, relay } = appConfig

const initState = {
  ws: null,
  lastPingTime: null,
  pollingIntervals: []
}

export default {
  state: initState,
  reducers: {
    update: (state, payload) => ({ ...state, ...payload }),
    initialize: (state, payload) => {
      // 重要：清空 pollingIntervals 前，一定要清空此定时器列表。其他地方也要注意
      _.forEach(state.pollingIntervals, inverval => clearInterval(inverval))
      return initState
    },
  },
  effects: dispatch => ({
    async start (payload, { socket, history }) {
      if (!("WebSocket" in window)) {
        // 浏览器不支持 WebSocket
        alert("您的浏览器不支持 WebSocket!");
      }

      // 建立新链接，要关闭旧的，防止内存泄露。
      let { ws } = socket
      if (ws) {
        ws.close()
      }

      ws = new WebSocket(relay.wsUrl)
      ws.onmessage = function(event) {
        var data = event.data;
        dispatch.socket.onMessage(data)
      };
      ws.onerror = () => {
        console.info('web socket 异常')
        ws.close();

        this.initialize()
        // 此处socket 异常，不用处理也行。因为心跳检查会兜底。此处只是为了及时处理。
        // 会死循环。暂时去掉。WebSocket is closed before the connection is established.
        // dispatch.socket.start()
      };
      ws.onopen = () => {
        console.info('web socket 建立成功')
        this.update({
          lastPingTime: moment()
        })
        // 暂时用轮询的方式
        dispatch.markets.onSocketReady()
        dispatch.balance.onSocketReady()
      }
      this.update({ws})

      dispatch.socket.polling()
    },
    async _onSubResult (payload) {
      let { args, result } = payload
      if (_.toUpper(result.status) === 'OK') return

      console.info('######## 订阅/去订阅失败')
      let { code, message } = result.error
      // 中继的 socket 是跟 apikey 绑定的。如果切换了账号，socket 要弃用。
      if (_.includes(message, 'Unmatched apikey') ||
          _.includes(message, 'Receive an illegal api key')) {
        dispatch.appState.update({
          notification: {
            type: 'error',
            message: 'notify.socket.subscribe.error.apikey',
            description: JSON.stringify(result.error),
          },
        })
        dispatch.dexAccount.logout()
        return
      }
      dispatch.appState.update({
        notification: {
          type: 'error',
          message: 'notify.socket.subscribe.error',
          description: JSON.stringify(result.error),
        },
      })

      // 重新订阅可能死循环。
      // dispatch.socket.subscribe(args)
    },
    async onMessage (payload, { socket, markets }) {

      if (payload === 'ping') {
        socket.ws.send('pong')
        this.update({
          lastPingTime: moment()
        })
        return
      }

      payload = JSON.parse(payload)
      if (payload.op === 'sub' || payload.op === 'unSub') {
        dispatch.socket._onSubResult(payload)
        return
      }
      if (!payload.op === '') return

      let { market: marketPair } = markets.currentMarket
      if (!marketPair) return

      let { topic: topicType, market: TopicMarket } = payload.topic
      if (topicType === 'account') {
        dispatch.balance.incrementalUpdate(payload.data)
      } else if (topicType === 'orderbook') {
        // depth&lrc-eth&0
        // 判断交易对是否是当前的
        if (_.upperCase(TopicMarket) !== _.upperCase(marketPair)) return
        dispatch.depth.incrementalUpdate(payload)
      } else if (topicType === 'trade') {
        // trade&lrc-eth
        // 判断交易对是否是当前的
        if (_.upperCase(TopicMarket) !== _.upperCase(marketPair)) return
        dispatch.trade.incrementalUpdate(payload.data)
      } else if (topicType === 'order') {
        dispatch.order.incrementalUpdate(payload.data)
        dispatch.order.incrementalUpdateProcessing(payload.data)
      } else if (topicType === 'candlestick') {
        // candlestick&LRC-ETH&1hr
        // 判断交易对是否是当前的
        if (_.upperCase(TopicMarket) !== _.upperCase(marketPair)) return
        dispatch.candleStick.incrementalUpdate(payload)
      } else if (topicType === 'ticker') {
        // ticker&LRC-BTC
        // 判断交易对是否是当前的
        if (_.upperCase(TopicMarket) !== _.upperCase(marketPair)) return
        dispatch.ticker.incrementalUpdate(payload)
      }
    },
    // 优化：
    // 切换账号后，换个apikey 订阅 account信息，就会报 apikey not match
    // 这里应该不用重新建立一个 socket 吧，直接通过新的 apikey 解析出新的 account 即可。
    // 亚东: 目前的设定是需要重新连接,之前认为直接更新订阅不是很安全,你们可以先close一下
    async subscribe (payload, { socket, dexAccount }) {
      if (!socket.ws || socket.ws.readyState !== 1) return

      let subInfo = {
        op: 'sub',
        topics: payload,
        apiKey: ENUM_ACCOUNT_STATUS.LOGGED_IN.equals(dexAccount.state) && dexAccount.apiKey || undefined
      }
      socket.ws.send(JSON.stringify(subInfo))
    },
    async unsubscribe (payload, { socket, dexAccount }) {
      if (!socket.ws || socket.ws.readyState !== 1) return

      let subInfo = {
        op: 'unSub',
        topics: payload,
        apiKey: ENUM_ACCOUNT_STATUS.LOGGED_IN.equals(dexAccount.state) && dexAccount.apiKey || undefined
      }
      socket.ws.send(JSON.stringify(subInfo))
    },
    async polling (payload, { socket }) {
      // utils.setImmediateInterval(() => {
      //   dispatch.depth.fetchDepth()
      //   dispatch.trade.fetchTrade()
      //   dispatch.order.fetchOrders()
      // }, pollingInterval);

      // this.subscribe(['candlestick&LRC-BTC&1Hour'])
      // utils.setImmediateInterval(() => dispatch.depth.fetchDepth(), 1000 * pollingIntervalRatio);
      // utils.setImmediateInterval(() => dispatch.trade.fetchTrade(), 1000 * pollingIntervalRatio);
      // utils.setImmediateInterval(() => dispatch.order.fetchOrders(), 1000 * pollingIntervalRatio);
      // utils.setImmediateInterval(() => dispatch.balance.fetchBalances(), 3000 * pollingIntervalRatio);

      let { pollingIntervals } = socket
      _.forEach(pollingIntervals, inverval => clearInterval(inverval))
      
      let intervals = []
      let interval = utils.setImmediateInterval(() => dispatch.rate.fetchRate(), 60 * 1000 * pollingIntervalRatio);
      intervals.push(interval)
      interval = utils.setImmediateInterval(() => dispatch.award.checkAward(), 10 * 1000 * pollingIntervalRatio);
      intervals.push(interval)
      // interval = utils.setImmediateInterval(() => dispatch.candleStick.fetchDayInfo(), 1000 * pollingIntervalRatio);
      // intervals.push(interval)
      // interval = utils.setImmediateInterval(() => dispatch.markets.fetchCurrentMarkets(), 3000 * pollingIntervalRatio);
      // intervals.push(interval)
      this.update({ pollingIntervals: intervals })
    },
    async checkHeartBeat (payload, { socket }) {
      let { ws, lastPingTime } = socket

      // 若socket还没创建，不检查。
      if (!ws || !lastPingTime) return
      // 距离上次心跳已经 3 分钟了，重新创建 socket。
      let now = moment()
      // 直接 lastPingTime.add 竟然会改掉 store !
      let validUntil = lastPingTime.clone().add(1, 'minutes')
      if (now.isBefore(validUntil)) return

      this.initialize()
      dispatch.socket.start()
      // dispatch.appState.update({
      //   notification: {
      //     type: 'error',
      //     message: `notify.socket.heartbeat.error`,
      //   },
      // })
    }
  })
}
