import _ from 'lodash'
import utils from 'utils'
import api from 'api';
import { store } from 'store'

const initState = {
  loading: false,
  list: [],
  page: 1,
  pageSize: 10
}

export default {
  state: initState,
  reducers: {
    initialize: (state, payload) => initState,
    update: (state, payload) => ({ ...state, ...payload }),
    appendBalance: (state, blance) => {
      let list = _.unionBy([ blance ], state.list, 'symbol')
      return {
        ...state,
        list: _.orderBy(list, 'symbol')
      }
    }
  },
  effects: dispatch => ({
    async fetchBalance (token, { dexAccount }) {
      this.update({ loading: true })

      let { symbol, name } = token
      let amount = 0
      if (symbol.toUpperCase() === 'ETH') {
        amount = await api.getEthBalance(dexAccount.address)
      } else {
        amount = await api.getTokenBalance(dexAccount.address, token.address)
      }
      if (!amount) {
        this.update({ loading: false })
        return
      }

      // amount = Number(
      //   config.fromWEI(
      //     tokenSymbol,
      //     amount,
      //     token.precision
      //   )
      // );
      // 不能用 fromWEI, 因为 fromWEI用了 toFixed,会导致四舍五入。全部划转会超。
      amount = utils.fromWEI(symbol, amount, null, true)

      let blance = {
        symbol,
        name,
        amount: amount,
      }
      // 必须如此使用，因为 fetchAllBalances 中的多个 fetchBalance 会并发，此处拿到的rootState是旧的（await 之前的）。
      // 而 reducers 没有此问题，reducers是可重入的。
      this.appendBalance(blance)
      this.update({ loading: false })

      // let { list } = rootState.walletBalance
      // list = _.unionBy([ blance ], list, 'symbol')

      // this.update({
      //   list
      // })
    },
    async fetchAllBalances (payload, { tokens }) {
      // 我的资产页面，进来时批量拉取
      // 转入页面，每选一个币种，单独 fetchBalance 一次。 此页面的select下拉框用本地 store。
      tokens.list.forEach(token => {
        // 暂时去掉 WETH
        if (token.symbol === 'WETH') return
        dispatch.walletBalance.fetchBalance(token)
      })
    },
  })
}